import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { SharedService } from 'src/app/services/shared.service';

@Component({
  selector: 'app-streamer-tools',
  templateUrl: './streamer-tools.component.html',
  styleUrls: ['./streamer-tools.component.scss']
})
export class StreamerToolsComponent implements OnInit {

  constructor(private router: Router,
    private sharedService: SharedService) { }

  ngOnInit(): void {
    this.sharedService.sendNavigationEvent(false);
    this.sharedService.sendNotificationEvent();
  }

  openPrizeWheel() {
    this.router.navigate(['./prize-wheel']);
  }

  openLeaderboard() {
    this.router.navigate(['./leaderboard']);
  }

}
