import { Injectable } from '@angular/core';
import { Observable, Subject } from 'rxjs';

@Injectable({
  providedIn: 'root'
})
export class SharedService {
  private notificationSubject = new Subject<any>();
  private searchSubject = new Subject<any>();
  private navigationSubject = new Subject<any>();

  sendNotificationEvent() {
    this.notificationSubject.next();
  }

  getNotificationEvent(): Observable<any> { 
    return this.notificationSubject.asObservable();
  }

  sendSearchEvent(query: string, users: any) {
    this.searchSubject.next({query, users});
  }

  getSearchEvent(): Observable<any> {
    return this.searchSubject.asObservable();
  }

  sendNavigationEvent(searchable: boolean) {
    this.navigationSubject.next(searchable);
  }

  getNavigationEvent(): Observable<any> {
    return this.navigationSubject.asObservable();
  }
}