import { Component, Input, OnInit, ViewChild } from '@angular/core';
import { NgbModal } from '@ng-bootstrap/ng-bootstrap';
import { NgxSpinnerService } from 'ngx-spinner';
import { ToastrService } from 'ngx-toastr';
import { finalize } from 'rxjs/operators';
import { SharedService } from 'src/app/services/shared.service';
import { TwitchService } from 'src/app/services/twitch.service';

@Component({
  selector: 'leaderboard',
  templateUrl: './leaderboard.component.html',
  styleUrls: ['./leaderboard.component.scss']
})
export class LeaderboardComponent implements OnInit {

  rows: any[] = [];
  leaderboards: any[] = [];
  title: string;
  leaderboard: any;

  constructor(private modalService: NgbModal,
    private twitchService: TwitchService,
    private toastr: ToastrService,
    private spinner: NgxSpinnerService,
    private sharedService: SharedService) { }

  ngOnInit(): void {
    this.sharedService.sendNotificationEvent();
    this.getLeaderboards();
    this.addRow();

    this.sharedService.sendNavigationEvent(false);
  }

  addRow() {
    this.rows.push({ player: '', score: ''});
  }

  removeRow() {
    this.rows.splice(-1, 1);
  }

  updateRow(input, index) {
    if (input.currentTarget.id == "player") {
      this.rows[index].player = input.currentTarget.value;
    } else if (input.currentTarget.id == "score") {
      this.rows[index].score = input.currentTarget.value;
    }
  }

  getLeaderboards() {
    this.spinner.show();
    this.twitchService.getLeaderboards().pipe(finalize(() => {
      this.spinner.hide();
    })).subscribe((data: any) => {
      this.leaderboards = data.leaderboards;
    }, err => {
      this.toastr.error('Unable to retrieve saved leaderboards.', 'Error');
    });
  }

  onLeaderboardSelection(leaderboardId) {
    this.leaderboard = this.leaderboards.filter(leaderboard => leaderboard.id == leaderboardId)[0];
    this.loadLeaderboard();
  }

  loadLeaderboard() {
    this.clearLeaderboard();
    this.title = this.leaderboard.title;
    this.leaderboard.leaderboardDetails.forEach(detail => {
      this.rows.push(detail);
    });
    this.addRow();
  }

  saveLeaderboard(title) {
    this.title = title;

    if (this.title && this.title != '') {
      this.addLeaderboardDetails();
      this.twitchService.saveLeaderboard(this.leaderboard).subscribe((data: any) => {
        this.toastr.info('Leaderboard saved.');
        this.getLeaderboards();
      }, err => {
        this.toastr.error('Unable to save leaderboard.', 'Error');
      });
    } else {
      this.toastr.error('Please provide a title for your leaderboard.');
    }
  }

  deleteLeaderboard() {
    if (this.leaderboard && this.leaderboard.id > 0) {
      this.twitchService.deleteLeaderboard(this.leaderboard.id).subscribe((data: any) => {
        this.clearLeaderboard(true);
        this.toastr.info('Leaderboard deleted.');
        this.getLeaderboards();
      }, err => {
        this.toastr.error('Unable to delete leaderboard.', 'Error');
      });
    } else {
      this.clearLeaderboard();
    }
  }

  addLeaderboardDetails() {
    if (!this.leaderboard) {
      this.leaderboard = { title: this.title,  };
    }
    this.leaderboard.leaderboardDetails = [];

    this.rows.forEach(row => {
      if (row.player != '' && row.score != '') {
        this.leaderboard.leaderboardDetails.push(row);
      }
    });
  }

  clearLeaderboard(hardClear: boolean = false) {
    this.rows = [];
    this.title = '';

    if (hardClear) {
      this.leaderboard = null;
    }
  }

  openModal(content) {
    this.modalService.open(content, {windowClass: 'modal-search'}).result.then((result) => {}, (reason) => {});
  }

}
