import { Injectable } from '@angular/core';
import { HttpClient, HttpHeaders, HttpParams } from '@angular/common/http';
import {environment} from 'src/environments/environment';
import { ToastrService } from 'ngx-toastr';
import { of } from 'rxjs';

@Injectable({
  providedIn: 'root'
})
export class TwitchService {

  clientId = 'kz6qzdszljw10pr9xynwv1acoo7dsy';
  authRedirectUrl: string;
  profileUrl: string;
  authToken: string;
  userInfo: any;

  constructor(private httpClient: HttpClient,
    private toastr: ToastrService) {
    this.authRedirectUrl = environment.authRedirectUrl;
    this.profileUrl = environment.profileUrl;
  }

  get isGuest(): boolean {
    let userId = localStorage.getItem('userId');
    return !userId;
  }

  getAuthUrl() {
    return `https://id.twitch.tv/oauth2/authorize?response_type=token&client_id=${this.clientId}&redirect_uri=${encodeURIComponent(this.authRedirectUrl)}&scope=user:edit:follows`;
  }

  clearLocalStorage() {
    localStorage.removeItem('profileImgUrl');
    localStorage.removeItem('auth');
    localStorage.removeItem('userId');
  }

  getUserId() {
    return localStorage.getItem('userId');
  }

  getProfileUrl(username) {
    return `${this.profileUrl}${username}`;
  }

  getProfileImgUrl() {
    let url = localStorage.getItem('profileImgUrl');
    if (!url) {
      url = "../../../assets/img/default-avatar.png";
    }
    return url;
  }

  checkIfLive(userId) {
    return this.httpClient.get(`${environment.apiUrl}/user/streamstatus?userId=${userId}`, 
      { headers: this.getHeaders() });
  }

  getFollowerCount(userId) {
    return this.httpClient.get(`${environment.apiUrl}/user/followers?userId=${userId}`, 
      { headers: this.getHeaders() });
  }

  checkIfFollowing(fromId, toId) {
    if (this.isGuest) {
      return of();
    }

    return this.httpClient.get(`${environment.apiUrl}/user/following?fromId=${fromId}&toId=${toId}`, 
      { headers: this.getHeaders() });
  }

  followUser(userId, currUserId) {
    if (this.isGuest) {
      this.toastr.error('You must be logged in to follow streamers.');
      return of();
    }

    return this.httpClient.post(`${environment.apiUrl}/user/follow?fromUserId=${currUserId}&toUserId=${userId}`, null,
      { headers: this.getHeaders() });
  }

  messageUser(messageNotification) {
    if (this.isGuest) {
      this.toastr.error('You must be logged in to message streamers.');
      return of();
    }

    return this.httpClient.post(`${environment.apiUrl}/user/notification`, messageNotification,
      { headers: this.getHeaders() });
  }

  loginAsGuest() {
    return this.httpClient.post(`${environment.apiUrl}/user/auth`, null,
      { headers: this.getHeaders() });
  }

  logout() {
    let bearer = sessionStorage.getItem('bearer');
    return this.httpClient.post(`https://api.twitch.tv/oauth2/revoke?client_id=${this.clientId}&token=${bearer}`, null,
      { headers: this.getHeaders() });
  }

  getHeaders() {
    return new HttpHeaders({
      'Content-Type': 'application/json',
      'Client-id': this.clientId,
      'Authorization': 'Bearer ' + (this.authToken ? this.authToken : localStorage.getItem('auth'))
    });
  }

  getUserData(pageNum, orderBy, ascending) {
    return this.httpClient.get(`${environment.apiUrl}/user?page=${pageNum}&orderBy=${orderBy}&ascending=${ascending}`, { headers: this.getHeaders() });
  }

  searchForUser(query, pageNum) {
    return this.httpClient.get(`${environment.apiUrl}/user/search?query=${query}&page=${pageNum}`, { headers: this.getHeaders() });
  }

  getNotifications() {
    if (this.isGuest) {
      return of();
    }

    let userId = localStorage.getItem('userId');
    return this.httpClient.get(`${environment.apiUrl}/user/notifications?userId=${userId}`, { headers: this.getHeaders() });
  }

  deleteNotification(notificationId) {
    if (this.isGuest) {
      return of();
    }

    return this.httpClient.put(`${environment.apiUrl}/user/notification?notificationId=${notificationId}`, null, { headers: this.getHeaders() });
  }

  getPrizeWheelItems() {
    let userId = localStorage.getItem('userId');
    return this.httpClient.get(`${environment.apiUrl}/user/prizeWheelItems?userId=${userId}`, { headers: this.getHeaders() });
  }

  savePrizeWheelItems(prizeWheelItems) {
    if (this.isGuest) {
      this.toastr.error('You must be logged in to save a custom prize wheel.');
      return of();
    }

    let userId = localStorage.getItem('userId');
    let params = new HttpParams();
    params = params.append('userId', userId);

    return this.httpClient.post(`${environment.apiUrl}/user/prizeWheelItems`, JSON.stringify(prizeWheelItems), { headers: this.getHeaders(), params: params });
  }

  getLeaderboards() {
    let userId = localStorage.getItem('userId');
    return this.httpClient.get(`${environment.apiUrl}/user/leaderboards?userId=${userId}`, { headers: this.getHeaders() });
  }

  saveLeaderboard(leaderboard) {
    if (this.isGuest) {
      this.toastr.error('You must be logged in to save a custom leaderboard.');
      return of();
    }

    let userId = localStorage.getItem('userId');
    let params = new HttpParams();
    params = params.append('userId', userId);

    return this.httpClient.post(`${environment.apiUrl}/user/leaderboard`, JSON.stringify(leaderboard), { headers: this.getHeaders(), params: params });
  }

  deleteLeaderboard(leaderboardId) {
    if (this.isGuest) {
      return of();
    }

    return this.httpClient.put(`${environment.apiUrl}/user/leaderboard?leaderboardId=${leaderboardId}`, null, { headers: this.getHeaders() });
  }

  getForumPosts() {
    return this.httpClient.get(`${environment.apiUrl}/user/forum`, { headers: this.getHeaders() });
  }

  postToForum(message) {
    if (this.isGuest) {
      this.toastr.error('You must be logged in to post to the forum.');
      return of();
    }

    let post = {
      twitchUserId: localStorage.getItem('userId'),
      message: message
    }

    return this.httpClient.post(`${environment.apiUrl}/user/forum`, JSON.stringify(post), { headers: this.getHeaders() });
  }

  deleteForumPost(postId) {
    if (this.isGuest) {
      this.toastr.error('You must be logged in to post to the forum.');
      return of();
    }

    return this.httpClient.put(`${environment.apiUrl}/user/forum?postId=${postId}`, null, { headers: this.getHeaders() });
  }
  
  validateToken() {
    return this.httpClient.get(`${environment.apiUrl}/user/validateToken`, { headers: this.getHeaders() });
  }
}