import { Component, OnInit, ViewChild } from '@angular/core';
import { NgxWheelComponent } from 'ngx-wheel';
import { NgbModal } from '@ng-bootstrap/ng-bootstrap';
import { TwitchService } from 'src/app/services/twitch.service';
import { ToastrService } from 'ngx-toastr';
import { finalize, min } from 'rxjs/operators';
import { NgxSpinnerService } from 'ngx-spinner';
import { SharedService } from 'src/app/services/shared.service';
import { transition, trigger, useAnimation } from '@angular/animations';
import { tada } from 'ng-animate';

@Component({
  selector: 'prize-wheel',
  templateUrl: './prize-wheel.component.html',
  styleUrls: ['./prize-wheel.component.scss'],
  animations: [
    trigger('tada', [transition('* => *', useAnimation(tada, { params: { timing: 3, delay: 0.5 } }))])
  ]
})
export class PrizeWheelComponent implements OnInit {
  @ViewChild(NgxWheelComponent, { static: false }) wheel;

  items: any[] = [];
  wheelItems: any[] = [];
  idToLandOn = 1;
  profileImgUrl: string;
  wheelSize: number;
  colors: string[];
  rendered = false;
  readyToSpin = false;
  alertAlignedToCenter = false;
  tada: any;

  constructor(private modalService: NgbModal,
    private twitchService: TwitchService,
    private toastr: ToastrService,
    private spinner: NgxSpinnerService,
    private sharedService: SharedService) { }

  ngOnInit(): void {
    this.sharedService.sendNotificationEvent();
    
    let wheelArea = document.getElementById('wheelArea');
    let size = Math.min(wheelArea.offsetWidth, window.innerHeight);
    this.wheelSize = size < 1000 ? size * 0.9 : size < 1100 ? size * 0.7 : size / 1.25;

    this.getItems();
    this.profileImgUrl = localStorage.getItem('profileImgUrl');

    if (localStorage.getItem('alertAlignedToCenter')) {
      this.alertAlignedToCenter = localStorage.getItem('alertAlignedToCenter') == 'true';
    } else {
      this.alertAlignedToCenter = false;
    }

    this.sharedService.sendNavigationEvent(false);
  }

  ngAfterViewInit() {
    this.readyToSpin = true;
  }

  changePrizeAlertAlignment() {
    this.alertAlignedToCenter = !this.alertAlignedToCenter;
    localStorage.setItem('alertAlignedToCenter', String(this.alertAlignedToCenter));
  }

  spin() {
    if (this.readyToSpin) {
      this.idToLandOn = this.getRandomItemId();
      this.readyToSpin = false;
      this.wheel.reset();
      this.wheel.idToLandOn = this.idToLandOn;
      this.wheel.spin();
    }
  }
  
  openSearchSettings(content) {
    this.modalService.open(content, {windowClass: 'modal-search'}).result.then((result) => {}, (reason) => {});
  }

  openPrizeSelectionSettings(content) {
    let windowClass = 'prize-selection';
    if (!this.alertAlignedToCenter) {
      windowClass = 'modal-search';
    }

    this.modalService.open(content, {windowClass: windowClass, container: '#wheelArea'}).result.then((result) => {}, (reason) => {});
  }

  saveWheelSettings() {
    if (this.items.length > 0) {
      this.twitchService.savePrizeWheelItems(this.items).subscribe((data: any) => {
        this.toastr.info('Prize wheel items saved.');
      }, err => {
        this.toastr.error('Unable to save prize wheel items.', 'Error');
      });
    } else {
      this.toastr.warning('At least one prize wheel item is required.', 'Error');
    }
  }

  resetItems() {
    this.getWheelItems();
    this.wheel.items = this.wheelItems;
    this.wheel.reset();
  }

  getItems() {
    this.spinner.show();
    let defaultItems = [{ itemText: 'GIFT A SUB', itemColor: '#32325d', itemTextColor: '#ffffff'},
      { itemText: 'PICK A GAME', itemColor: '#221a42', itemTextColor: '#ffffff'},
      { itemText: 'GIFT AN EMOTE', itemColor: '#32325d', itemTextColor: '#ffffff'},
      { itemText: 'PLAY A GAME', itemColor: '#a55dfd', itemTextColor: '#ffffff'}];
    this.twitchService.getPrizeWheelItems().pipe(finalize(() => {
      this.resetItems();
      this.spinner.hide();
      this.rendered = true;
    })).subscribe((data: any) => {
      if (data.prizeWheelItems && data.prizeWheelItems.length > 0) {
        this.items = data.prizeWheelItems;
      } else {
        this.items = defaultItems;
      }
    }, err => {
      this.toastr.error('Unable to retrieve saved prize wheel items.', 'Error');
      this.items = defaultItems;
    });
  }

  getWheelItems() {
    this.wheelItems = [];
    let id = 0;
    let fontSize = this.wheelSize / 40;
    this.items.forEach(item => {
      this.wheelItems.push({
        'fillStyle': item.itemColor, 
        'text': item.itemText, 
        'id': id, 
        'textFontSize' : fontSize, 
        'textFillStyle': item.itemTextColor
      });
      id++;
    });
  }

  onSpinStart() {

  }

  onSpinComplete() {
    this.readyToSpin = true;
  }

  getRandomItemId() {
    let min = 0;
    let max = this.items.length - 1;
    return Math.floor(Math.random() * (max - min + 1) + min);
  }

  addRow() {
    this.items.push({ itemText: '', itemColor: '#a55dfd', itemTextColor: '#FFFFFF' });
    this.resetItems();
  }

  removeRow() {
    this.items.splice(-1, 1);
    this.resetItems();
  }

  updateText(input, index) {
    if (input.currentTarget.name == "itemText") {
      this.items[index].itemText = input.currentTarget.value;
    }
    this.resetItems();
  }

  updateItemColor(color, index) {
    this.items[index].itemColor = color;
    this.resetItems();
  }

  updateItemTextColor(color, index) {
    this.items[index].itemTextColor = color;
    this.resetItems();
  }
}
