import { Component, OnInit } from '@angular/core';
import { NgxSpinnerService } from 'ngx-spinner';
import { ToastrService } from 'ngx-toastr';
import { finalize } from 'rxjs/operators';
import { SharedService } from 'src/app/services/shared.service';
import { TwitchService } from 'src/app/services/twitch.service';

@Component({
  selector: 'app-forum',
  templateUrl: './forum.component.html',
  styleUrls: ['./forum.component.scss']
})
export class ForumComponent implements OnInit {
  forumPosts: any;
  notifications: any[];

  constructor(private twitchService: TwitchService,
    private toastr: ToastrService,
    private spinner: NgxSpinnerService,
    private sharedService: SharedService) {
  }

  ngOnInit() {
    this.sharedService.sendNotificationEvent();
    this.getForumPosts();
  }

  getDatetime(date) {
    let convertedDate = new Date(date);
    let newDate = new Date(convertedDate.toString() + ' UTC');
    return newDate.toLocaleString();
  }

  postToForum(userMessage) {
    this.twitchService.postToForum(userMessage.value).subscribe((data: any) => {
      this.toastr.info('Message posted to forum.');
      userMessage.value = '';
      this.getForumPosts();
    }, err => {
      this.toastr.error('Unable to post to forum.', 'Error');
    });
  }

  getForumPosts() {
    this.spinner.show();
    this.twitchService.getForumPosts().pipe(finalize(() => {
      this.spinner.hide();
    })).subscribe((data: any) => {
      this.forumPosts = data.posts;
    }, () => {
      this.toastr.error('Unable to get forum posts.', 'Error');
    });
  }

  allowPostDeletion(post) {
    return post.user.userId == this.twitchService.getUserId();
  }

  deleteForumPost(id) {
    this.twitchService.deleteForumPost(id).subscribe(() => {
      this.getForumPosts();
    }, () => {
      this.toastr.error('Unable to delete the forum post.', 'Error');
    });
  }
}
